<template>
  <div class="events">
     <div class="text-xs-center">
      <v-progress-circular
       v-if="loading"
      indeterminate
      color="primary"
    ></v-progress-circular>
      </div>
    <div class="formodal">
      <v-dialog v-model="dialog" max-width="800px">
         <v-btn fab color="primary" dark slot="activator" class="mb-2">
          <v-icon dark>add</v-icon>
        </v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
           <v-alert
                :value="alert"
                color="error"
                icon="error_circle"
                outline
                >{{alertMsg}}</v-alert>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field label="예배명" v-model="editedItem.title"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                    <v-select
                        label="부서"
                        :items="belong_items"
                        v-model="belong_items[editedItem.belongs]"
                        single-line
                        item-text="text"
                        item-value="id"
                        return-object
                        persistent-hint
                        @change="changeBelong" 
                    ></v-select>
                    </v-flex>
                <v-flex xs12 sm6 md4>
                    <v-menu
                    ref="date_menu"
                    :close-on-content-click="false"
                    v-model="date_menu"
                    :nudge-right="40"
                    :return-value.sync="editedItem.day"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <v-text-field
                        slot="activator"
                        v-model="editedItem.day"
                        label="일시"
                        prepend-icon="event"
                        readonly
                    ></v-text-field>
                    <v-date-picker locale='ko-KR' v-model="editedItem.day" no-title scrollable>
                        <v-btn flat color="primary" @click="date_menu = false">취소</v-btn>
                        <v-btn flat color="primary" @click="$refs.date_menu.save(editedItem.day)">확인</v-btn>
                    </v-date-picker>
                    </v-menu>
                </v-flex>
                 <v-flex xs12 sm6 md4>
                  <v-text-field label="장소" v-model="editedItem.place"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field label="재적 총원" v-model="editedItem.totalmember"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click.native="close">취소</v-btn>
            <v-btn color="blue darken-1" flat @click.native="save">저장</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <!-- <template>
    <v-app id="dayspan" v-cloak>
        <ds-calendar-app :calendar="this.calendar"></ds-calendar-app>
    </v-app>
    </template> -->
    <v-data-table
      :headers="headers"
      :items="events"
      :pagination.sync="pagination"
      :rows-per-page-items="pagination.rowsPerPageItems"
      :total-items="pagination.totalItems"
    >
      <template slot="items" slot-scope="props">
        <!-- <td class="text-xs-left">{{ props.item.id }}</td> -->
        <td class="text-xs-left"  style="min-width:100px">{{ belong_text(props.item.belongs) }}</td>
        <td class="text-xs-left">{{ props.item.title }}</td>
        <td class="text-xs-left">{{ props.item.day }}</td>
        <td class="text-xs-left" style="min-width:120px">{{ props.item.place }}</td>
        <td class="text-xs-left">{{ props.item.totalmember }}</td>
        <td class="justify-center layout px-0">
          <v-btn icon class="mx-0" @click="editItem(props.item)">
            <v-icon color="teal">edit</v-icon>
          </v-btn>
          <v-btn icon class="mx-0" @click="deleteEvents(props.item)">
            <v-icon color="pink">delete</v-icon>
          </v-btn>
        </td>
      </template>
      <template slot="no-data">
        <v-btn color="primary" @click="getevents">Reset</v-btn>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import apiService from '@/Services/ApiService'
import Datepicker from 'vuejs-datepicker';

export default {
  name: 'events',
  components:{
    Datepicker
  },
  data () {
    return {
      alert: false,
      alertMsg:"",
      loading:false,
      events: [],
      dialog: false,
      cdate:new Date(),
      belong_items: [
        { text: '유치부', id: 0 },
        { text: '유초등부', id: 1 },
        { text: '중고등부', id: 2 },
        { text: '청년부', id: 3 },
        // { text: '면려회', id: '4' }
      ],
      pagination: {
        page: 1,
        rowsPerPage: 10,
        totalItems: 0,
        rowsPerPageItems: [5, 10, 15, 20, 25]
      },
      cbelong: -1,
      editedIndex: -1,
      date_menu:false,
      editedItem: {
        id: '',
        title: '',
        place: '',
        day: this.cdate,
        belongs : this.cbelong,
        totalmember : 10
      },
      headers: [
        {
          text: '소속',
          align: 'left',
          sortable: true,
          value: 'place'
        },
        {
          text: '예배명',
          align: 'left',
          sortable: true,
          value: 'title'
        },
        {
          text: '일시',
          align: 'left',
          sortable: true,
          value: 'day'
        },
         {
          text: '장소',
          align: 'left',
          sortable: false,
          value: 'place'
        },
        {
          text: ' 재적 총원',
          align: 'left',
          sortable: true,
          value: 'totalmember'
        },
        { text: '편집', value: 'title', sortable: false }
      ]
    }
  },
  mounted () {
    this.getevents()
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? '새 예배' : '정보 수정'
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  methods: {
      belong_text:function(belong){
          if(belong===0) return  '유치부'
          if(belong===1) return  '유초등부'
          if(belong===2) return  '중고등부'
          if(belong===3) return  '청년부'
      },
    async getevents () {
      this.loading = true;
      const response = await apiService.fetchEvents()
      this.events = response.data
      this.loading = false;
    },
    async changeBelong(selectObj) {
        console.log('chagne belong '+selectObj.id)
        this.editedItem.belongs = selectObj.id
        this.getTotalMember()
     },
     onSelected(selected){
        this.cdate = selected
    },
    async getTotalMember(){
       const response = await apiService.fetchNames(this.editedItem.belongs)
       var names = response.data.filter(mem =>{
           return (mem.grade == 0 )
        })
       this.editedItem.totalmember = names.length;
       console.log(this.editedItem.totalmember )
    },

    async save () {
      try {
         apiService.checkEvents(this.editedItem)
        .then((result) => {
          if(result.data != "") {//error
             this.alert = true
              this.alertMsg = "이미 등록된 이벤트 입니다"
              setTimeout(() => {
                  this.alert = !this.alert
              }, 3000)
          }else{
             if (this.editedIndex === -1) {
                apiService.addEvents(this.editedItem)
                .then((result) => {
                    this.editedItem = Object.assign({}, result.data)
                    this.events.push(this.editedItem)
                })
             }else{
              apiService.updateEvents(this.editedItem)
              Object.assign(this.events[this.editedIndex], this.editedItem)
            }
            this.close()
          }
        })
      } catch (err) {
        return console.log(err.message)
      } finally {
        
      }
    },

    async deleteEvents (events) {
      const $this = this
      $this.$swal({
        title: '삭제 하시겠습니까',
        text: "삭제되면 다시 되돌릴 수 없습니다",
        type: 'warning',
        showCancelButton: true,
        cancelButtonText: '아니요',
        confirmButtonText: '네 삭제 합니다'
      }).then((result) => {
        if (result.value) {
          apiService.deleteEvents(events.id)
          const index = this.events.indexOf(events)
          this.events.splice(index, 1)
        }
      })
    },

    editItem (item) {
      this.editedIndex = this.events.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    }
  }
}
</script>

<style>
body, html, #app, #dayspan {
  font-family: Roboto, sans-serif;
  width: 100%;
  height: 100%;
}
th {
  background-color: #dedede;
  color: rgba(255, 255, 255, 0.66);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
</style>



